'use strict';

/**
* Fill a strided array with pseudorandom numbers drawn from an inverse gamma distribution.
*
* @module @stdlib/random-strided-invgamma
*
* @example
* import Float64Array from '@stdlib/array-float64';
* import invgamma from '@stdlib/random-strided-invgamma';
*
* // Create an array:
* var out = new Float64Array( 10 );
*
* // Fill the array with pseudorandom numbers:
* invgamma( out.length, [ 2.0 ], 0, [ 5.0 ], 0, out, 1 );
*
* @example
* import Float64Array from '@stdlib/array-float64';
* import invgamma from '@stdlib/random-strided-invgamma';
*
* // Create an array:
* var out = new Float64Array( 10 );
*
* // Fill the array with pseudorandom numbers:
* invgamma.ndarray( out.length, [ 2.0 ], 0, 0, [ 5.0 ], 0, 0, out, 1, 0 );
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-nonenumerable-read-only-property';
import main from './main.js';
import ndarray from './ndarray.js';


// MAIN //

setReadOnly( main, 'ndarray', ndarray );


// EXPORTS //

export default main;

// exports: { "ndarray": "main.ndarray" }
